import * as THREE from 'three'
import jsLogo      from '../../img/js-min.jpg'
import mongodbLogo from '../../img/mongodb-min.jpg'
import nodejsLogo  from '../../img/nodejs-min.jpg'
import reactjsLogo from '../../img/reactjs-min.jpg'
import expressLogo from '../../img/express-min.jpg'
import htmlLogo    from '../../img/html5-min.jpg'

/*******************************************************************************
 * 3D анимация, куб.
 */

export default (canvas)=>{
  const scene = new THREE.Scene()
  const aspect = canvas.offsetWidth/canvas.offsetHeight
  const fov = 75, near = 0.1, far = 10000
  const camera = new THREE.PerspectiveCamera(fov, aspect, near, far)
  camera.position.z = 2

  const renderer = new THREE.WebGLRenderer( {canvas, alpha:true } )
  renderer.setClearColor( 0xffffff, 0.0)

  const boxWidth = 1
  const boxHeight = 1
  const boxDepth = 1
  const geometry = new THREE.BoxBufferGeometry(boxWidth, boxHeight, boxDepth)

  const loader = new THREE.TextureLoader()

  const materials = [
    new THREE.MeshBasicMaterial({map: loader.load(jsLogo)}),
    new THREE.MeshBasicMaterial({map: loader.load(expressLogo)}),
    new THREE.MeshBasicMaterial({map: loader.load(nodejsLogo)}),
    new THREE.MeshBasicMaterial({map: loader.load(reactjsLogo)}),
    new THREE.MeshBasicMaterial({map: loader.load(mongodbLogo)}),
    new THREE.MeshBasicMaterial({map: loader.load(htmlLogo)})
  ]

  const cube = new THREE.Mesh(geometry, materials)
  scene.add(cube)

  function resizeRendererToDisplaySize(renderer) {
    const canvas = renderer.domElement
    const w = canvas.offsetWidth
    const h = canvas.offsetHeight
    const needResize = canvas.width !== w || canvas.height !== h
    if (needResize) {
      renderer.setSize(w, h, false)
    }
    return needResize
  }

  function render() {
    if (resizeRendererToDisplaySize(renderer)) {
      const canvas = renderer.domElement
      camera.aspect = canvas.width / canvas.height
      camera.updateProjectionMatrix()
    }
    cube.rotation.x += 0.004
    cube.rotation.y += 0.005
    renderer.render(scene, camera)
    requestAnimationFrame(render)
  }

 requestAnimationFrame(render)
}
