import * as THREE from 'three'
import OrbitControls from 'threejs-orbit-controls'
import right  from '../../img/skybox/right.jpg'
import center from '../../img/skybox/center.jpg'
import left   from '../../img/skybox/left.jpg'
// import top    from '../../img/skybox/top.jpg'
// import bottom from '../../img/skybox/bottom.jpg'
// import back   from '../../img/skybox/back.jpg'

/*******************************************************************************
 * Скайбокс
 * Skybox
 */

export default (canvas)=>{
  const scene = new THREE.Scene()
  const renderer = new THREE.WebGLRenderer( {canvas, alpha:true } )
  const aspect = canvas.offsetWidth/canvas.offsetHeight
  const fov = 75, near = 0.1, far = 10000
  const camera = new THREE.PerspectiveCamera(fov, aspect, near, far)
  camera.position.z = 2

  // move the mouse
  const controls = new OrbitControls(camera, renderer.domElement)

  const geometry = new THREE.CubeGeometry(10000, 10000, 10000)

  const cubeMaterials = [
    new THREE.MeshBasicMaterial( {map: new THREE.TextureLoader().load(right),  side: THREE.DoubleSide } ),
    new THREE.MeshBasicMaterial( {map: new THREE.TextureLoader().load(left),   side: THREE.DoubleSide } ),
  //  new THREE.MeshBasicMaterial( {map: new THREE.TextureLoader().load(top),    side: THREE.DoubleSide } ),
  //  new THREE.MeshBasicMaterial( {map: new THREE.TextureLoader().load(bottom), side: THREE.DoubleSide } ),
  //  new THREE.MeshBasicMaterial( {map: new THREE.TextureLoader().load(back),   side: THREE.DoubleSide } ),
    new THREE.MeshBasicMaterial( {map: new THREE.TextureLoader().load(center), side: THREE.DoubleSide } ),
  ]

  const cubeMaterial = new THREE.MeshFaceMaterial( cubeMaterials )
  const cube = new THREE.Mesh(geometry, cubeMaterials)
  scene.add(cube)

  function resizeRendererToDisplaySize(){
    const canvas = renderer.domElement
    const w = canvas.offsetWidth
    const h = canvas.offsetHeight
    const needResize = canvas.width !== w || canvas.height !== h
    if(needResize){
      renderer.setSize(w, h, false)
      camera.aspect = w / h
      camera.updateProjectionMatrix()
    }
  }

  // logic
  const update = function(){
    cube.rotation.x += 0.005
    cube.rotation.y += 0.001
    controls.update()
    resizeRendererToDisplaySize()
  }

  // draw
  const render = function(){
    renderer.render(scene, camera)
  }

  // run loop
  const loop = function(){
    requestAnimationFrame(loop)
    update()
    render()
  }

  loop()
}
