import React, {useEffect, useState} from 'react'
import styles from './canvas.module.scss'

export default ({method, isDecoration})=>{
  let mount, cls

  const [isBig, setBig] = useState(false)

  function handler(){
    setBig(!isBig)
  }

  useEffect(()=>{
    method(mount)
  }, [isBig] )

  return isDecoration ?
    <canvas className={ styles.decoration } ref={ el=> mount=el } /> :
    <canvas className={ isBig ? styles.big : styles.mini } onClick={handler} ref={ el=> mount=el } />
}
